"use client"

import { useState, useRef } from "react"
import { parseGPX, interpolatePoints } from "@/lib/gpx-parser"
import { processVideoWithGPX } from "@/lib/video-processor"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import FrameAnalysis from "./FrameAnalysis"

interface VideoFrame {
  id: string
  timestamp: number
  distance: number
  coordinates: {
    latitude: number
    longitude: number
  }
  imageUrl: string
}

interface Damage {
  id: string
  frameId: string
  type: string
  severity: string
  area: number
  rectangle: {
    x: number
    y: number
    width: number
    height: number
  }
}

interface Segment {
  startDistance: number
  endDistance: number
  damages: Damage[]
  pci?: {
    value: number
    rating: string
    maintenance: string
  }
}

interface VideoGPXAnalysisProps {
  onDamagesFound?: (damages: Array<{
    id: string;
    type: string;
    severity: string;
    quantity: number;
    unit: string;
    latitude: string;
    longitude: string;
  }>) => void;
}

const SEGMENT_LENGTH = 50; // 50 meters per segment

export default function VideoGPXAnalysis({ onDamagesFound }: VideoGPXAnalysisProps) {
  const [videoFile, setVideoFile] = useState<File | null>(null)
  const [gpxFile, setGpxFile] = useState<File | null>(null)
  const [processing, setProcessing] = useState(false)
  const [progress, setProgress] = useState(0)
  const [currentStep, setCurrentStep] = useState<string>("")
  const [frames, setFrames] = useState<VideoFrame[]>([])
  const [segments, setSegments] = useState<Segment[]>([])
  const [damages, setDamages] = useState<Damage[]>([])
  
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)

  const handleVideoUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      if (file.size > 500 * 1024 * 1024) { // 500MB limit
        alert("Ukuran video maksimal 500MB")
        return
      }
      if (!file.type.startsWith("video/")) {
        alert("File harus berupa video (MP4/MOV/AVI)")
        return
      }
      setVideoFile(file)
    }
  }

  const handleGPXUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      if (!file.name.endsWith('.gpx')) {
        alert("File harus berformat GPX")
        return
      }
      setGpxFile(file)
    }
  }

  const handleDamageAdded = (damage: Damage) => {
    setDamages(prev => [...prev, damage])
    
    // Update segments
    const segmentIndex = Math.floor(
      frames.find(f => f.id === damage.frameId)?.distance ?? 0 / SEGMENT_LENGTH
    )
    
    const newSegments = [...segments]
    if (!newSegments[segmentIndex]) {
      newSegments[segmentIndex] = {
        startDistance: segmentIndex * SEGMENT_LENGTH,
        endDistance: (segmentIndex + 1) * SEGMENT_LENGTH,
        damages: []
      }
    }
    
    newSegments[segmentIndex].damages.push(damage)
    setSegments(newSegments)

    // Calculate PCI for the segment
    if (onDamagesFound) {
      const segmentDamages = newSegments[segmentIndex].damages.map(d => ({
        id: d.id,
        type: d.type,
        severity: d.severity,
        quantity: d.area,
        unit: "m¬≤",
        latitude: frames.find(f => f.id === d.frameId)?.coordinates.latitude.toString() ?? "",
        longitude: frames.find(f => f.id === d.frameId)?.coordinates.longitude.toString() ?? ""
      }))
      onDamagesFound(segmentDamages)
    }
  }

  const processVideoAndGPX = async () => {
    if (!videoFile || !gpxFile) {
      alert("Mohon upload video dan file GPX terlebih dahulu")
      return
    }

    setProcessing(true)
    setProgress(0)
    setCurrentStep("Membaca file GPX...")

    try {
      // Read GPX file
      const gpxText = await gpxFile.text()
      setProgress(10)
      setCurrentStep("Menganalisis data GPX...")
      
      const gpxData = parseGPX(gpxText)
      setProgress(20)
      setCurrentStep("Menghitung titik interpolasi...")

      // Get points at 5m intervals
      const interpolatedPoints = interpolatePoints(gpxData, 5)
      setProgress(30)
      setCurrentStep("Mempersiapkan ekstraksi frame...")

      // Process video frames with GPX data
      const processPoints = interpolatedPoints.map((point, index) => ({
        time: point.time?.getTime() ?? Date.now(),
        distance: index * 5, // 5m intervals
        coordinates: {
          latitude: point.latitude,
          longitude: point.longitude
        }
      }));

      setCurrentStep("Memproses video dan mengekstrak frame...")
      // Process video frames
      const frameUrls = await processVideoWithGPX(
        videoFile,
        processPoints,
        (videoProgress) => {
          // Scale progress from 40% to 90%
          setProgress(40 + (videoProgress * 0.5))
          setCurrentStep(`Mengekstrak frame video... (${Math.round(videoProgress)}%)`)
        }
      );

      setProgress(90)
      setCurrentStep("Menyiapkan hasil...")

      // Create frame objects with metadata
      const newFrames = processPoints.map((point, index) => ({
        id: crypto.randomUUID(),
        timestamp: point.time,
        distance: point.distance,
        coordinates: point.coordinates,
        imageUrl: frameUrls[index]
      }));

      setFrames(newFrames);

      // Initialize segments
      const totalDistance = newFrames[newFrames.length - 1].distance;
      const numSegments = Math.ceil(totalDistance / SEGMENT_LENGTH);
      const initialSegments: Segment[] = Array.from({ length: numSegments }, (_, i) => ({
        startDistance: i * SEGMENT_LENGTH,
        endDistance: (i + 1) * SEGMENT_LENGTH,
        damages: []
      }));
      setSegments(initialSegments);

      setProgress(100)
      setCurrentStep("Selesai!")
      alert("Proses sinkronisasi video dan GPX berhasil!")
    } catch (error) {
      console.error("Error processing files:", error)
      alert("Terjadi kesalahan saat memproses file")
    } finally {
      setTimeout(() => {
        setProcessing(false)
        setProgress(0)
        setCurrentStep("")
      }, 1000)
    }
  }

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Video Upload Section */}
        <Card className="p-6 bg-purple-900/20 border-purple-500/30">
          <h4 className="text-lg font-semibold text-white mb-4">Upload Video Survey</h4>
          <div className="border-2 border-dashed border-gray-500/30 rounded-lg p-6">
            <input
              type="file"
              id="video"
              accept="video/mp4,video/mov,video/avi"
              onChange={handleVideoUpload}
              className="hidden"
            />
            <label
              htmlFor="video"
              className="flex flex-col items-center justify-center cursor-pointer"
            >
              <div className="text-4xl mb-2">üé•</div>
              <div className="text-center text-white">
                Klik untuk upload video survey jalan
              </div>
              <p className="text-sm text-gray-400 mt-2">
                Format: MP4, MOV, AVI (Maks. 500MB)
              </p>
            </label>
          </div>
          {videoFile && (
            <div className="mt-4">
              <p className="text-sm text-white">
                Video: {videoFile.name}
              </p>
            </div>
          )}
        </Card>

        {/* GPX Upload Section */}
        <Card className="p-6 bg-purple-900/20 border-purple-500/30">
          <h4 className="text-lg font-semibold text-white mb-4">Upload File GPX</h4>
          <div className="border-2 border-dashed border-gray-500/30 rounded-lg p-6">
            <input
              type="file"
              id="gpx"
              accept=".gpx"
              onChange={handleGPXUpload}
              className="hidden"
            />
            <label
              htmlFor="gpx"
              className="flex flex-col items-center justify-center cursor-pointer"
            >
              <div className="text-4xl mb-2">üìç</div>
              <div className="text-center text-white">
                Klik untuk upload file GPX track
              </div>
              <p className="text-sm text-gray-400 mt-2">
                Format: GPX file dari GPS recorder
              </p>
            </label>
          </div>
          {gpxFile && (
            <div className="mt-4">
              <p className="text-sm text-white">
                GPX: {gpxFile.name}
              </p>
            </div>
          )}
        </Card>
      </div>

      {/* Processing Controls */}
      <div className="flex flex-col gap-4">
        <Button
          onClick={processVideoAndGPX}
          disabled={!videoFile || !gpxFile || processing}
          className="w-full bg-blue-600 hover:bg-blue-700 h-12"
        >
          {processing ? 'üîÑ SEDANG MEMPROSES...' : 'üöÄ PROSES VIDEO & GPX'}
        </Button>
      </div>

      {/* Progress Bar */}
      {processing && (
        <div className="space-y-2">
          <Progress value={progress} className="h-2" />
          <div className="flex justify-between items-center text-sm">
            <p className="text-white">{currentStep}</p>
            <p className="text-white font-mono">{Math.round(progress)}%</p>
          </div>
        </div>
      )}

      {/* Preview Canvas - Hidden but used for processing */}
      <canvas ref={canvasRef} className="hidden" />
      <video ref={videoRef} className="hidden" />

      {/* Extracted Frames Display */}
      {frames.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h4 className="text-lg font-semibold text-white">Frame yang Diekstrak:</h4>
            <p className="text-sm text-gray-400">
              Total: {frames.length} frame ({(frames.length * 5)}m)
            </p>
          </div>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {frames.map((frame) => (
              <FrameAnalysis
                key={frame.id}
                frame={frame}
                segmentDistance={SEGMENT_LENGTH}
                onDamageAdded={handleDamageAdded}
              />
            ))}
          </div>
        </div>
      )}

      {/* Segments Summary */}
      {segments.length > 0 && (
        <div className="space-y-4">
          <h4 className="text-lg font-semibold text-white">Analisis per Segmen:</h4>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {segments.map((segment, index) => (
              <Card key={index} className="p-4 bg-black/50">
                <h5 className="font-semibold text-white">
                  Segmen {index + 1} ({segment.startDistance}m - {segment.endDistance}m)
                </h5>
                <div className="mt-2 space-y-1 text-sm text-gray-300">
                  <p>Jumlah Kerusakan: {segment.damages.length}</p>
                  {segment.pci && (
                    <>
                      <p>PCI: {segment.pci.value}</p>
                      <p>Rating: {segment.pci.rating}</p>
                      <p>Rekomendasi: {segment.pci.maintenance}</p>
                    </>
                  )}
                </div>
              </Card>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}
