interface ProjectData {
  projectInfo: {
    roadName: string;
    section: string;
    sketch: string;
    roadNumber: string;
    roadWidth: string;
    sectionLength: string;
    surveyor: string;
    date: string;
  };
  damages: Array<{
    id: string;
    type: string;
    severity: string;
    quantity: number;
    unit: string;
    latitude?: string;
    longitude?: string;
  }>;
  pciResult?: {
    pci: number;
    rating: string;
    maintenance: string;
  };
}

export function exportToJSON(data: ProjectData): string {
  try {
    const jsonString = JSON.stringify(data, null, 2);
    const blob = new Blob([jsonString], { type: 'application/json' });
    return URL.createObjectURL(blob);
  } catch (error) {
    console.error('Error exporting data:', error);
    throw new Error('Failed to export data');
  }
}

