export async function importFromJSON(file: File): Promise<ProjectData> {
  try {
    const text = await file.text();
    const data = JSON.parse(text) as ProjectData;
    
    // Validate imported data
    if (!data.projectInfo || !data.damages) {
      throw new Error('Invalid data format');
    }
    
    return data;
  } catch (error) {
    console.error('Error importing data:', error);
    throw new Error('Failed to import data');
  }
}

export function validateProjectData(data: ProjectData): string[] {
  const errors: string[] = [];

  // Validate project info
  if (!data.projectInfo.roadName) errors.push('Nama ruas jalan harus diisi');
  if (!data.projectInfo.section) errors.push('Seksi harus dipilih');
  if (!data.projectInfo.roadWidth) errors.push('Lebar jalan harus diisi');
  if (!data.projectInfo.sectionLength) errors.push('Panjang seksi harus diisi');
  if (!data.projectInfo.surveyor) errors.push('Nama petugas survai harus diisi');
  if (!data.projectInfo.date) errors.push('Tanggal harus diisi');

  // Validate damages
  if (data.damages.length === 0) {
    errors.push('Minimal satu data kerusakan harus diinput');
  }

  data.damages.forEach((damage, index) => {
    if (!damage.type) errors.push(`Jenis kerusakan #${index + 1} harus diisi`);
    if (!damage.severity) errors.push(`Tingkat keparahan #${index + 1} harus diisi`);
    if (damage.quantity <= 0) errors.push(`Kuantitas #${index + 1} harus lebih besar dari 0`);
    if (!damage.unit) errors.push(`Satuan #${index + 1} harus diisi`);
  });

  return errors;
}

export function formatDate(date: string): string {
  return new Date(date).toLocaleDateString('id-ID', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });
}
