interface GPXPoint {
  latitude: number;
  longitude: number;
  elevation?: number;
  time?: Date;
}

interface GPXTrack {
  name?: string;
  points: GPXPoint[];
}

export function parseGPX(gpxContent: string): GPXTrack {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(gpxContent, "text/xml");
  
  // Get track points
  const trackPoints = Array.from(xmlDoc.getElementsByTagName("trkpt"));
  
  const points: GPXPoint[] = trackPoints.map(point => {
    const lat = parseFloat(point.getAttribute("lat") || "0");
    const lon = parseFloat(point.getAttribute("lon") || "0");
    
    // Get elevation if available
    const eleElement = point.getElementsByTagName("ele")[0];
    const elevation = eleElement ? parseFloat(eleElement.textContent || "0") : undefined;
    
    // Get timestamp if available
    const timeElement = point.getElementsByTagName("time")[0];
    const time = timeElement ? new Date(timeElement.textContent || "") : undefined;
    
    return {
      latitude: lat,
      longitude: lon,
      elevation,
      time
    };
  });

  // Get track name if available
  const nameElement = xmlDoc.getElementsByTagName("name")[0];
  const name = nameElement ? nameElement.textContent || undefined : undefined;

  return {
    name,
    points
  };
}

export function calculateDistance(point1: GPXPoint, point2: GPXPoint): number {
  const R = 6371e3; // Earth's radius in meters
  const œÜ1 = (point1.latitude * Math.PI) / 180;
  const œÜ2 = (point2.latitude * Math.PI) / 180;
  const ŒîœÜ = ((point2.latitude - point1.latitude) * Math.PI) / 180;
  const ŒîŒª = ((point2.longitude - point1.longitude) * Math.PI) / 180;

  const a = Math.sin(ŒîœÜ/2) * Math.sin(ŒîœÜ/2) +
          Math.cos(œÜ1) * Math.cos(œÜ2) *
          Math.sin(ŒîŒª/2) * Math.sin(ŒîŒª/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

  return R * c; // Distance in meters
}

export function interpolatePoints(track: GPXTrack, interval: number): GPXPoint[] {
  const interpolatedPoints: GPXPoint[] = [];
  let totalDistance = 0;
  
  // Calculate total track distance
  for (let i = 0; i < track.points.length - 1; i++) {
    totalDistance += calculateDistance(track.points[i], track.points[i + 1]);
  }

  // Calculate number of points needed
  const numPoints = Math.floor(totalDistance / interval);
  
  // Interpolate points at regular intervals
  let currentDistance = 0;
  let currentIndex = 0;
  
  for (let i = 0; i <= numPoints; i++) {
    const targetDistance = i * interval;
    
    // Find the segment containing the target distance
    while (currentIndex < track.points.length - 1) {
      const segmentDistance = calculateDistance(
        track.points[currentIndex],
        track.points[currentIndex + 1]
      );
      
      if (currentDistance + segmentDistance >= targetDistance) {
        // Interpolate point within this segment
        const ratio = (targetDistance - currentDistance) / segmentDistance;
        const point = interpolatePosition(
          track.points[currentIndex],
          track.points[currentIndex + 1],
          ratio
        );
        interpolatedPoints.push(point);
        break;
      }
      
      currentDistance += segmentDistance;
      currentIndex++;
    }
  }

  return interpolatedPoints;
}

function interpolatePosition(start: GPXPoint, end: GPXPoint, ratio: number): GPXPoint {
  const lat = start.latitude + (end.latitude - start.latitude) * ratio;
  const lon = start.longitude + (end.longitude - start.longitude) * ratio;
  const time = start.time && end.time ? 
    new Date(start.time.getTime() + (end.time.getTime() - start.time.getTime()) * ratio) :
    undefined;
  const ele = start.elevation && end.elevation ?
    start.elevation + (end.elevation - start.elevation) * ratio :
    undefined;

  return {
    latitude: lat,
    longitude: lon,
    time,
    elevation: ele
  };
}

export function getFrameTimestamps(track: GPXTrack, interval: number): number[] {
  const points = interpolatePoints(track, interval);
  return points
    .map(point => point.time?.getTime())
    .filter((time): time is number => time !== undefined);
}
