interface ProcessPoint {
  time: number;
  distance: number;
  coordinates: {
    latitude: number;
    longitude: number;
  };
}

export async function extractFrameFromVideo(
  video: HTMLVideoElement,
  canvas: HTMLCanvasElement,
  point: ProcessPoint,
  totalDuration: number,
  totalDistance: number
): Promise<string> {
  return new Promise((resolve, reject) => {
    try {
      // Calculate time position based on distance proportion
      const distanceRatio = point.distance / totalDistance;
      const timePosition = distanceRatio * totalDuration;

      // Set video to calculated position
      video.currentTime = timePosition;

      // When video is ready at the new position
      const onSeeked = () => {
        try {
          // Set canvas size to match video
          canvas.width = video.videoWidth;
          canvas.height = video.videoHeight;

          // Draw video frame to canvas
          const ctx = canvas.getContext('2d');
          if (!ctx) {
            reject(new Error('Could not get canvas context'));
            return;
          }

          // Draw the frame
          ctx.drawImage(video, 0, 0, canvas.width, canvas.height);

          // Add coordinate and distance overlay
          ctx.fillStyle = 'rgba(0, 0, 0, 0.7)';
          ctx.fillRect(10, 10, 250, 80);
          ctx.fillStyle = 'white';
          ctx.font = '14px monospace';
          ctx.fillText(`Distance: ${point.distance.toFixed(1)}m`, 15, 30);
          ctx.fillText(`Lat: ${point.coordinates.latitude.toFixed(6)}¬∞`, 15, 50);
          ctx.fillText(`Lon: ${point.coordinates.longitude.toFixed(6)}¬∞`, 15, 70);

          // Convert canvas to data URL
          const dataUrl = canvas.toDataURL('image/jpeg', 0.8);
          
          // Clean up event listener
          video.removeEventListener('seeked', onSeeked);
          
          resolve(dataUrl);
        } catch (error) {
          reject(error);
        }
      };

      // Add event listeners
      video.addEventListener('seeked', onSeeked);
      video.onerror = () => {
        video.removeEventListener('seeked', onSeeked);
        reject(new Error('Error seeking video'));
      };
    } catch (error) {
      reject(error);
    }
  });
}

export function createVideoElement(videoFile: File): Promise<HTMLVideoElement> {
  return new Promise((resolve, reject) => {
    const video = document.createElement('video');
    video.src = URL.createObjectURL(videoFile);
    video.onloadedmetadata = () => {
      // Ensure video is ready to play
      video.load();
      resolve(video);
    };
    video.onerror = () => reject(new Error('Error loading video'));
  });
}

export async function processVideoWithGPX(
  videoFile: File,
  gpxPoints: ProcessPoint[],
  onProgress: (progress: number) => void
): Promise<string[]> {
  const video = await createVideoElement(videoFile);
  const canvas = document.createElement('canvas');
  const frames: string[] = [];

  try {
    // Wait for video metadata to load
    await new Promise<void>((resolve) => {
      video.onloadedmetadata = () => resolve();
    });

    // Calculate total distance
    const totalDistance = gpxPoints[gpxPoints.length - 1].distance;
    const totalDuration = video.duration;

    // Process frames with delay between each extraction
    for (let i = 0; i < gpxPoints.length; i++) {
      const point = gpxPoints[i];
      
      try {
        // Add small delay between frame extractions to prevent browser hanging
        await new Promise(resolve => setTimeout(resolve, 100));
        
        const frame = await extractFrameFromVideo(video, canvas, point, totalDuration, totalDistance);
        frames.push(frame);
        
        // Update progress
        onProgress((i + 1) / gpxPoints.length * 100);
      } catch (error) {
        console.error(`Error extracting frame at ${point.distance}m:`, error);
        // Use a blank frame with coordinates as fallback
        const blankFrame = createBlankFrameWithCoordinates(point);
        frames.push(blankFrame);
      }
    }

    return frames;
  } finally {
    // Clean up
    URL.revokeObjectURL(video.src);
  }
}

function createBlankFrameWithCoordinates(point: ProcessPoint): string {
  const canvas = document.createElement('canvas');
  canvas.width = 640;
  canvas.height = 480;
  const ctx = canvas.getContext('2d');
  
  if (ctx) {
    // Fill background
    ctx.fillStyle = '#000000';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    
    // Add coordinate text
    ctx.fillStyle = 'rgba(255, 255, 255, 0.9)';
    ctx.font = '16px monospace';
    ctx.fillText(`Distance: ${point.distance.toFixed(1)}m`, 20, 40);
    ctx.fillText(`Lat: ${point.coordinates.latitude.toFixed(6)}¬∞`, 20, 70);
    ctx.fillText(`Lon: ${point.coordinates.longitude.toFixed(6)}¬∞`, 20, 100);
  }
  
  return canvas.toDataURL('image/jpeg', 0.8);
}
