"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { exportToJSON, importFromJSON, generatePDFReport } from "@/lib/data-handler"

interface ActionButtonsProps {
  onImport: (data: any) => void;
  getData: () => any;
}

export default function ActionButtons({ onImport, getData }: ActionButtonsProps) {
  const [importing, setImporting] = useState(false)

  const handleExport = () => {
    try {
      const data = getData()
      const url = exportToJSON(data)
      
      // Create temporary link and trigger download
      const link = document.createElement('a')
      link.href = url
      link.download = `survey-pci-${new Date().toISOString().split('T')[0]}.json`
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)
    } catch (error) {
      console.error('Export error:', error)
      alert('Terjadi kesalahan saat mengexport data')
    }
  }

  const handleImport = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    try {
      setImporting(true)
      const data = await importFromJSON(file)
      onImport(data)
    } catch (error) {
      console.error('Import error:', error)
      alert('Terjadi kesalahan saat mengimport data')
    } finally {
      setImporting(false)
      // Reset input
      event.target.value = ''
    }
  }

  const handlePrint = () => {
    try {
      const data = getData()
      generatePDFReport(data)
    } catch (error) {
      console.error('Print error:', error)
      alert('Terjadi kesalahan saat mencetak laporan')
    }
  }

  return (
    <div className="flex gap-4">
      <Button 
        onClick={handleExport}
        className="bg-blue-600 hover:bg-blue-700"
      >
        üì§ EXPORT DATA
      </Button>
      
      <div className="relative">
        <input
          type="file"
          accept=".json"
          onChange={handleImport}
          className="hidden"
          id="import-file"
          disabled={importing}
        />
        <label htmlFor="import-file">
          <Button 
            className="bg-yellow-600 hover:bg-yellow-700 cursor-pointer"
            disabled={importing}
            asChild
          >
            <span>
              {importing ? 'Importing...' : 'üì• IMPORT DATA'}
            </span>
          </Button>
        </label>
      </div>

      <Button 
        onClick={handlePrint}
        className="bg-green-600 hover:bg-green-700"
      >
        üñ®Ô∏è PRINT LAPORAN
      </Button>
    </div>
  )
}
